/* https://piccalil.li/blog/a-modern-css-reset */

/* Box sizing rules */

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */

body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */

ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */

html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */

a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */

img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */

input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Arial", sans-serif;
  color: #FFFFFF;
  font-size: 16px;
}

.container {
  max-width: 1514px;
  margin: 0 auto;
  width: 100%;
  padding-inline: 15px;
}

a[class] {
  text-decoration: none;
}

ul[class] {
  list-style-type: none;
}

button {
  background-color: transparent;
  border: 0;
  padding: 0;
  cursor: pointer;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.btn {
  border-radius: 16px;
  font-size: 14px;
  transition: all 0.3s ease-in-out;
  padding: 10px;
  min-height: 46px;
  min-width: 123px;
}

.btn-more {
  color: #000000;
  min-width: 284px;
  min-height: 60px;
  background-color: #FF97B3;
  border-radius: 30px;
  font-style: italic;
  font-size: 20px;
  line-height: 23px;
  display: inline-flex;
  padding: 10px 15px;
  align-items: center;
  justify-content: center;
}

.btn-more:hover,
.btn-more:focus {
  background-color: #ff4b7b;
}

.hero-card__header {
  margin-bottom: 16px;
}

.hero-card__socials {
  background-color: #DBD978;
  display: inline-flex;
  padding: 5px 18px;
  border-radius: 16px;
  column-gap: 22px;
  margin: 0;
}

.hero-card__img-wrapper {
  display: flex;
  justify-content: center;
  padding: 15px 17px;
  max-width: 309px;
  margin: 0 auto 16px;
}

.hero-card__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
  margin-bottom: 8px;
}

.hero-card__subtitle {
  font-size: 18px;
  line-height: 21px;
  text-align: center;
}

.gradient1 {
  background: linear-gradient(to bottom, #FFDD22 50%, #383030 50%);
}

.gradient2 {
  background: linear-gradient(to bottom, #780D1A 50%, #F73821 50%);
}

.gradient3 {
  background: linear-gradient(to bottom, #FF97B3 50%, #E55C21 50%);
}

.gradient4 {
  background: linear-gradient(to bottom, #A3C7ED 50%, #0A2640 50%);
}

.hero {
  background-color: #173817;
  padding: 40px 0;
}

.hero__cards {
  display: flex;
}

.about {
  background-color: #FF97B3;
  padding: 27px 0 26px;
}

.about__img {
  display: flex;
  align-items: center;
  justify-content: center;
}

.about__img svg {
  height: auto;
  max-width: 100%;
  display: block;
}

.about__info {
  background-color: #780D1A;
  padding: 15px 25px;
}

.about__title {
  font-size: 42px;
  line-height: 54px;
  border-bottom: 1px solid #FFFFFF;
  margin-bottom: 15px;
  padding-bottom: 15px;
}

.about__text {
  margin-bottom: 32px;
  line-height: 1.5;
  color: rgba(255, 255, 255, 0.8);
}

.about__text p:not(:last-child) {
  margin-bottom: 16px;
}

@keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
  }
}

.about__img:hover #wings {
  transform-origin: 42.9% 35.2%;
  animation: rotation 5s linear infinite;
}

.contacts {
  background-color: #B3DFB3;
  padding: 36px 0 36px;
}

.contacts__title {
  font-weight: 700;
  font-size: 32px;
  line-height: 34px;
  color: #31363D;
  text-align: center;
  margin-bottom: 32px;
}

.contact-form {
  display: flex;
  flex-direction: column;
  max-width: 790px;
  margin: 0 auto;
}

.contact-form__label {
  color: #31363D;
  font-family: "Lato", sans-serif;
  font-size: 20px;
  line-height: 24px;
  display: inline-flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 32px;
}

.contact-form__input {
  font-size: 18px;
  padding: 18px 24px;
  background-color: #FFFFFF;
  margin: 0;
  border: 2px solid transparent;
  border-radius: 16px;
  outline: 0;
  margin: 8px 0;
}

.contact-form__input::placeholder {
  color: #A9A9A9;
  font-size: 18px;
}

.contact-form__input:hover {
  border-color: #D6D7D8;
}

.contact-form__input:focus {
  border-color: #00B56D;
}

.contact-form__input.is-error {
  border-color: #D1003F;
}

.contact-form__input.is-error::placeholder {
  color: #D1003F;
}

.contact-form__input.success::placeholder {
  color: #007244;
}

.contact-form__textarea {
  font-size: 18px;
  padding: 18px 24px;
  background-color: #FFFFFF;
  margin: 0;
  border: 2px solid transparent;
  border-radius: 16px;
  outline: 0;
  resize: none;
  margin: 8px 0;
}

.contact-form__textarea::placeholder {
  color: #A9A9A9;
  font-size: 18px;
}

.contact-form__textarea:hover {
  border-color: #D6D7D8;
}

.contact-form__textarea:focus {
  border-color: #00B56D;
}

.contact-form__textarea.is-error {
  border-color: #D1003F;
}

.contact-form__textarea.is-error::placeholder {
  color: #D1003F;
}

.contact-form__textarea.success::placeholder {
  color: #007244;
}

.contact-form__footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
}

.contact-form__submit {
  font-family: "Lato", sans-serif;
  background-color: #008751;
  font-weight: 900;
  font-size: 20px;
  line-height: 22px;
  border-radius: 16px;
  padding: 18px 24px;
  color: #FFFFFF;
}

.contact-form__submit--disabled {
  background-color: #A9A9A9;
}

.contact-form__error-message {
  font-size: 13px;
  line-height: 15px;
  display: inline-block;
  color: #D1003F;
}

.contact-form__error-footer {
  width: 100%;
  text-align: right;
}

@media screen and (min-width: 85.375em) {
  .about__info {
    background-image: url("../../img/bg-info.svg");
    background-color: transparent;
    background-repeat: no-repeat;
    background-size: contain;
  }
}

@media screen and (min-width: 64em) {
  .container {
    padding-inline: 80px;
  }

  .hero {
    padding: 80px 0;
  }

  .hero__card {
    width: 25%;
  }

  .about {
    padding: 47px 0 46px;
  }

  .about__inner {
    gap: 44px;
    display: flex;
  }

  .about__img {
    width: 50%;
  }

  .about__info {
    width: 50%;
    padding: 68px 47px;
  }

  .about__title {
    font-size: 82px;
    line-height: 94px;
    margin-bottom: 32px;
    padding-bottom: 30px;
  }

  .contacts {
    padding: 80px 0 36px;
  }

  .contacts__title {
    font-size: 48px;
    line-height: 42px;
  }
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}